import React, {Component, Fragment} from 'react';
import {inject, observer} from "mobx-react";
import {EditorState, convertToRaw} from 'draft-js';
import {Editor} from 'react-draft-wysiwyg';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
import '../../node_modules/react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

import {Button, withStyles} from "@material-ui/core";
import SendIcon from '@material-ui/icons/Send';
import TextField from '@material-ui/core/TextField';

import GoogleOAuth from '../Plugin/GoogleOAuth.js';
import forumAjaxAdapter from '../Adapters/ForumAjaxAdapter';
import ZlibHelper from '../Utils/ZlibHelper';

const styles = theme => ({
    textField: {
        marginRight: theme.spacing.unit,
    },
    sendIcon: {
        marginLeft: theme.spacing.unit
    }
});

function removeDefaultContentProps(rawContent) {
    rawContent.blocks.map((val) => {
        if (val.type === 'unstyled') delete val.type;
        if (val.depth === 0) delete val.depth;
        if (val.inlineStyleRanges.length === 0) delete val.inlineStyleRanges;
        if (val.entityRanges.length === 0) delete val.entityRanges;
        if (Object.keys(val.data).length === 0) delete val.data;
        return val;
    });
    return rawContent;
}

@inject('navState', 'createReplyActionState', 'threadListState', 'threadBodyState', 'notificationSnackState')
@observer
class EditorConvertToHTML extends Component {
    state = {
        title: '',
        threadTitleInputErr: false,
        editorState: EditorState.createEmpty(),
    };

    onEditorStateChange = (editorState) => {
        this.setState({
            editorState,
        });
    };

    handleChange = name => event => {
        this.setState({[name]: event.target.value});
    };

    submitNewThread = async () => {
        const {createReplyActionState, threadListState, notificationSnackState} = this.props;
        const title = this.state.title.trim();
        if (!title) {
            this.setState({threadTitleInputErr: true});
            notificationSnackState.triggerOpen('Title cannot contain space only!');
            return;
        }
        const content = JSON.stringify(convertToRaw(this.state.editorState.getCurrentContent()));
        const contentObj = convertToRaw(this.state.editorState.getCurrentContent());
        removeDefaultContentProps(contentObj);
        const content2 = await ZlibHelper.gzip(JSON.stringify(contentObj));
        // console.log(JSON.stringify(contentObj));
        // console.log(draftToHtml(convertToRaw(this.state.editorState.getCurrentContent())))
        const time = new Date().getTime().toString();
        const signBase64 = GoogleOAuth.signSignature(Buffer.concat([
            Buffer.from(GoogleOAuth.getUserInfo().userAddr, 'utf8'),
            Buffer.from(title, 'utf8'),
            Buffer.from(content2, 'utf8'),
            Buffer.from(time, 'utf8'),
        ]));
        forumAjaxAdapter.submitNewThread(GoogleOAuth.getUserInfo().userAddr, title, content2, time, signBase64)
            .then(() => {
                createReplyActionState.setModalEditorClose();
                threadListState.callUpdateThreadList();
                notificationSnackState.triggerOpen('Create new thread successfully!');
            });
    };

    submitNewReply = async () => {
        const {createReplyActionState, threadBodyState, notificationSnackState} = this.props;
        const {userAddr} = GoogleOAuth.getUserInfo();
        const contentObj = convertToRaw(this.state.editorState.getCurrentContent());
        removeDefaultContentProps(contentObj);
        const content2 = await ZlibHelper.gzip(JSON.stringify(contentObj));
        const time = new Date().getTime().toString();
        const signBase64 = GoogleOAuth.signSignature(Buffer.concat([
            Buffer.from(userAddr, 'utf8'),
            Buffer.from(threadBodyState.threadAddr, 'utf8'),
            Buffer.from(content2, 'utf8'),
            Buffer.from(time, 'utf8'),
        ]));
        forumAjaxAdapter.submitNewReply(userAddr, threadBodyState.threadAddr, content2, time, signBase64)
            .then(() => {
                createReplyActionState.setModalEditorClose();
                threadBodyState.callUpdateThreadBody();
                notificationSnackState.triggerOpen('Reply successfully!');
            });
    };

    submitToBC = () => {
        const {navState} = this.props;
        if (navState.createReplyBtnState === 'add') this.submitNewThread();
        else if (navState.createReplyBtnState === 'reply') this.submitNewReply();
    };

    render() {
        const {classes, navState, createReplyActionState} = this.props;
        const {editorState} = this.state;
        const titleField = navState.createReplyBtnState === 'add' ? (
            <TextField
                required
                fullWidth
                label="Title"
                className={classes.textField}
                margin="normal"
                autoComplete="off"
                onChange={this.handleChange('title')}
                error={this.state.threadTitleInputErr}
            />
        ) : '';
        return (
            <Fragment>
                {titleField}
                <Editor
                    editorState={editorState}
                    wrapperClassName="demo-wrapper"
                    editorClassName="demo-editor"
                    onEditorStateChange={this.onEditorStateChange}
                    editorStyle={{border: '2px solid #F1F1F1', height: '30vh'}}
                />
                {/*<textarea*/}
                {/*disabled*/}
                {/*value={draftToHtml(convertToRaw(editorState.getCurrentContent()))}*/}
                {/*/>*/}
                {/*<Editor toolbarHidden editorState={editorState}  readOnly={true}/>*/}
                {/*{JSON.stringify(editorState.getCurrentContent())}*/}
                <Button variant="contained" onClick={this.submitToBC} style={{backgroundColor: "#ffc400"}}>
                    {navState.getModalEditorBtnTxt}
                    <SendIcon className={classes.sendIcon}/>
                </Button>
            </Fragment>
        );
    }
}

export default withStyles(styles)(EditorConvertToHTML);